@import '../../scss/base/mixins';
@import '../../scss/base/variables';

.container {
  position: absolute;
  left: 0px;
  right: 0px;

  display: flex;
  justify-content: space-between;
  z-index: 100;

  min-width: 360px;
  max-width: 1440px;

  padding: 62px 16px 0px;
  margin: 0 auto;
  background-color: transparent;
  @include mobile-only {
  }
}

.logoIcon {
  width: 48px;
  height: 32px;
  fill: $primary-black;
  transition: $transition-default;
  @include desktop-only {
    width: 72px;
    height: 50px;
  }
}

svg:hover {
  fill: $primary-white;
}
